.profile__name {
  margin: 0;
  padding: 0;
  max-width: 294px;
  font-weight: 500;
  font-size: 42px;
  line-height: 114%;
  text-overflow: ellipsis;
  white-space: nowrap;
  overflow: hidden;

}

@media screen and (max-width: 420px) {
  .profile__name {
    max-width: 189px;
    font-size: 27px;
    line-height: 1.2;
    justify-self: end;


  }
}
